(function() {
  'use strict';

  angular
    .module('portfolio')
    .run(runBlock)
    .animation('.reveal-animation', function() {
  return {
    enter: function(element, done) {
      element.css('display', 'none');
      element.fadeIn(500, done);
      return function() {
        element.stop();
      }
    },
    leave: function(element, done) {
      element.fadeOut(500, done)
      return function() {
        element.stop();
      }
    }
  }
});

  /** @ngInject */
  function runBlock($log, $FB) {

    $log.debug('runBlock end');
    $FB.init('1619673508300494');
  }

})();

(function() {
  'use strict';

  angular
    .module('portfolio')
    .config(routerConfig);

  /** @ngInject */
  function routerConfig($stateProvider, $urlRouterProvider) {
    $stateProvider
      .state('home', {
        url: '/',
        templateUrl: 'app/main/main.html',
        controller: 'PostController',
        controllerAs: 'post'
      })
      .state('about', {
        url: '/about',
        templateUrl: 'app/main/about.html',
        controller: 'PostController',
        controllerAs: 'about'
      })
      .state('portfolio', {
        url: '/portfolio',
        templateUrl: 'app/main/portfolio.html',
        controller: 'MainController',
        controllerAs: 'main'
      })
      .state('blog', {
        url: '/blog',
        templateUrl: 'app/main/blog.html',
        controller: 'MainController',
        controllerAs: 'main'
      })
      .state('viewPost', {
        url: '/blog/post/:slug',
        templateUrl: 'app/main/viewPost.html',
        controller: 'PostController',
        controllerAs: 'blogPost'
      })
      .state('adminLogin', {
        url: '/admin/login',
        templateUrl: 'app/admin/login.html',
        controller: 'LoginController',
        controllerAs: 'login'
      })
      .state('adminDashboard', {
        url: '/admin/dashboard',
        templateUrl: 'app/admin/dashboard.html',
        controller: 'LoginController',
        controllerAs: 'login'
      })
      .state('addPost', {
        url: '/admin/post/add',
        templateUrl: 'app/admin/addPost.html',
        controller: 'PostController',
        controllerAs: 'post'
      })
      .state('updatePost', {
        url: '/admin/post/update/:slug',
        templateUrl: 'app/admin/editPost.html',
        controller: 'PostController',
        controllerAs: 'post'
      })
      ;


    $urlRouterProvider.otherwise('/');
  }

})();

/* global malarkey:false, moment:false */
(function() {
  'use strict';

  angular
    .module('portfolio')
    .constant('malarkey', malarkey)
    .constant('moment', moment)
    .constant('SERVICE_URL', 'https://sumeetbajra.herokuapp.com');

})();

(function() {
  'use strict';

  angular
    .module('portfolio')
    .config(config);

  /** @ngInject */
  function config($logProvider, toastrConfig, markedProvider, $locationProvider) {
    // Enable log
    $logProvider.debugEnabled(true);

    // Set options third-party lib
    toastrConfig.allowHtml = true;
    toastrConfig.timeOut = 3000;
    toastrConfig.positionClass = 'toast-top-right';
    toastrConfig.preventDuplicates = true;
    toastrConfig.progressBar = true;

      $locationProvider.html5Mode({
            enabled: true,
            //requireBase: false
      });

    //hljs
      markedProvider.setOptions({
            gfm: true,
            tables: true,
            highlight: function (code, lang) {
                  if (lang) {
                        return hljs.highlight(lang, code, true).value;
                  } else {
                        return hljs.highlightAuto(code).value;
                  }
            }
      });

  }

})();

